// -----------------------------//
//          VARIABLES           //
// -----------------------------//

$services__bubble: $color1;
$services-service__text: $color1;
$services-service__marker: $color12;
$services-service__icon-wrapper__bgc: #FBEDDD;
$services__separator: #B4CBD7;

// -----------------------------//
//            STYLES            //
// -----------------------------//

.services {
	margin-top: 4.5rem;

	@include xxl {
		background-image: url('../img/services-bg-1.png'), url('../img/services-bg-2.png');
		background-repeat: no-repeat;
		background-position: left 30%, right 71%;
	}

	&__heading {
		margin-bottom: 2.4rem;
	}
	
	&-bubble {
		background: #fbfdfd; /* Old browsers */
		background: -moz-linear-gradient(left,  #fbfdfd 10%, #ECF4F8 90%); /* FF3.6-15 */
		background: -webkit-linear-gradient(left,  #fbfdfd 10%, #ECF4F8 90%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to right,  #fbfdfd 10%, #ECF4F8 90%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fbfdfd', endColorstr='#ECF4F8',GradientType=1 ); /* IE6-9 */
		border-radius: 2rem;
		border-bottom-right-radius: 0;
		font: {
			family: "Open Sans", sans-serif;
			size: 1.875rem;
			weight: 400;
		}
		line-height: 1.4;
		color: $services__bubble;
		padding: 2.9rem 2rem 2.5rem 2rem;

		b {
			font: {
				weight: 600;
			}
		}

		&__shape {
			width: 7.1875rem;
			height: auto;
			margin-bottom: 0.5rem;
		}
	}

	&__img {
		width: 100%;
		height: auto;
		margin-top: 4rem;

		@include md {
			width: 140%;
			margin-top: 0;
		}

		@include xxl {
			width: 170%;
		}

		@include xxxl {
			width: 652px;
		}
	}

	&-service {

		&--narrow {

			@include md {
				padding-right: 6%;
			}
		}

		&__heading {
			font: {
				size: 1.5rem;
			}
			margin-bottom: 1.2rem;
		}

		&__columns {

			@include md {
				column-count: 2;
				column-gap: 3rem;
				-moz-column-count: 2;
			}
		}

		&__icon {
			@include flex-center;
			background-color: $how-it-works__icon__bgc;
			background-position: center center;
			background-repeat: no-repeat;
			width: 6.5625rem;
			height: 6.5625rem;
			border-radius: 0.65rem;
			box-shadow: 0px 17px 30px 0px rgba(3, 77, 111, 0.13);
			z-index: 2;
			margin-bottom: 1.25rem;

			@include lg {
				margin-bottom: 1.5rem;
			}

			@include xl {
				margin-bottom: 2rem;
			}

			&--1 {
				background-image: url('../img/services-service-icon-1.svg');
				background-size: 70%;
				background-position: center 56%;
			}

			&--2 {
				background-image: url('../img/services-service-icon-2.svg');
				background-size: 65%;
			}

			&--3 {
				background-image: url('../img/services-service-icon-3.svg');
				background-size: 77%;
				background-position: center 59%;
			}

			&--4 {
				background-image: url('../img/services-service-icon-4.svg');
				background-size: 72%;
				background-position: center 61%;
			}
		}

		p {
			color: $services-service__text;
		}

		&__ul {
			margin-top: -0.4rem;
			color: $services-service__text;
			padding-left: 1.6rem;
			list-style-type: none;
		}

		li {
			position: relative;

			&::before {
				content: "\22C5";
				display: inline-block;
				margin-left: -1.3rem;
				margin-right: 0.9rem;
				font: {
					size: 1.5em;
				}
				line-height: 0;
				color: $services-service__marker;
				transform: translateY(1px);

				@include md {
					transform: translateY(2px);
				}

				@include lg {
					transform: translateY(1px);
				}
			}
		}

		&__subheading {
			font: {
				family: "Open Sans", sans-serif;
				size: 1.125rem;
				weight: 600;
			}
			color: $services-service__text;

			@include md {
				margin-top: 1.5rem;
			}

			@include lg {
				margin-top: 0;
			}
		}

		&__icon-wrapper {
			width: 3.125rem;
			height: 3.125rem;
			background-color: $services-service__icon-wrapper__bgc;
			border-radius: 50%;
			@include flex-center;
			margin: 0.8rem 0 0.55rem 0;
		}

		&__icon-svg {
			width: 1.5rem;
			height: auto;
		}

		&__benefits {
			font: {
				size: 14px;
			}
			line-height: 1.3;
		}
	}

	&__separator {
		height: 3px;
		background-color: $services__separator;
		margin: 5.1rem 0 4.9rem 0;
	}
}